<template>
  <div id="app">
    <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css"/>
    <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css">
    
    <div>
        
        <ul class="nav_main">
            <li>
                <div><img :src="images.logoSrc" /></div>
            </li>
            <li>
                <div v-bind:class="{ active_page: showAppComparison }" @click="changePage('showAppComparison')"><i class="fa fa-globe fa-2x"></i><br>App Comparison</div>
            </li>
            <li>
                <div v-bind:class="{ active_page: showCustomerFeedback }" @click="changePage('showCustomerFeedback')"><i class="fa fa-comments-o fa-2x"></i><br>Customer Feedback</div>
            </li>
            <li>
                <div v-bind:class="{ active_page: showMobileDownload }" @click="changePage('showMobileDownload')"><i class="fa fa-mobile fa-2x"></i><br>Mobile Download</div>
            </li>
            
            <li style="float:right">
                <div @click="logout"><i><img style="border-radius:50%" :src="images.profilePic"></i><br>Logout</div>
            </li>
            
        </ul>
    </div>
    
    <div v-if="showAppComparison">
        <app-comparison></app-comparison>
    </div>
    <div v-if="showCustomerFeedback">
        <customer-feedback></customer-feedback>
    </div>
    <div v-if="showMobileDownload">
        <mobile-download></mobile-download>
    </div>
    
  </div>
</template>

<script>

    import AppComparison from './main_pages/App-Comparison.vue'
    import CustomerFeedback from './main_pages/Customer-Feedback.vue'
    import MobileDownload from './main_pages/Mobile-Download.vue'
    
    import axios from 'axios'
    
    export default {
        name: 'app',
        mounted(){
            let this_vm = this
            
//            let test_pic = 'http://s3.amazonaws.com/37assets/svn/765-default-avatar.png'
            
            setTimeout(()=>{
                axios.get('/getProfile')
                    .then((response)=>{
//                    console.log(response.data)
                    this_vm.images.profilePic = response.data
                })
            }, 1000)
            
//            this_vm.images.profilePic = test_pic
        },
        data() {
            return {
                images: {
                    logoSrc: require("assets/img/logo.png"),
                    profilePic: ''
                },
                showAppComparison: true,
                showCustomerFeedback: false,
                showMobileDownload: false
            }
        },
         methods: {
            changePage(page){
                this.showAppComparison = false
                this.showCustomerFeedback = false
                this.showMobileDownload = false
                
                this[page] = true
            },
            logout(){
                window.location = "/logout"
            }
        },
        components: {
            AppComparison,
            CustomerFeedback,
            MobileDownload
        }
    }
</script>

<style scoped>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        background-color: white;
    }
    
    .row {
        margin: 0;
    }

    img {
        height: 40px;
    }
    

    .nav_main a {
        text-decoration: none;
    }

    .nav_main {
        list-style-type: none;
        background-color: #072146;
        text-align: left;
    }

    .nav_main li {
        display: inline-block;
/*        color: white;*/
        color: #337ab7;
        text-align: center;
        vertical-align: middle;
        padding: 10px 15px;
    }
    
    .nav_main li:hover {
        cursor: pointer;
        color: white;
    }

    .active_page {
        color: white;
    }
</style>



